body {
    margin: 0;
    padding: 0;
    background-color: #4B92DB;
    overflow: hidden;
}

.map-container {
    width: 100%;
    height: 100%;
    position: relative;
}

.wind-arrow {
    position: absolute;
    width: 6px;
    height: 6px;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 50%;
    transform-origin: center;
    pointer-events: none;
    transition: all 0.3s ease;
}

.wind-arrow::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: inherit;
    border-radius: inherit;
    opacity: 0.7;
    filter: blur(2px);
    animation: trail 1s linear infinite;
}

@keyframes trail {
    0% {
        transform: scale(1) translateX(0);
        opacity: 0.7;
    }
    100% {
        transform: scale(0.1) translateX(-30px);
        opacity: 0;
    }
}

.wind-arrow.colliding {
    animation: collide 0.3s ease-out;
}

@keyframes collide {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.5);
    }
    100% {
        transform: scale(1);
    }
}

#world-map {
    position: relative;
    width: 100%;
    height: 100%;
}