body {
  background-color: #4b92db;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.map-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.wind-arrow {
  transform-origin: center;
  pointer-events: none;
  background: #fffc;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  transition: all .3s;
  position: absolute;
}

.wind-arrow:after {
  content: "";
  background: inherit;
  border-radius: inherit;
  opacity: .7;
  filter: blur(2px);
  width: 100%;
  height: 100%;
  animation: 1s linear infinite trail;
  position: absolute;
}

@keyframes trail {
  0% {
    opacity: .7;
    transform: scale(1)translateX(0);
  }

  100% {
    opacity: 0;
    transform: scale(.1)translateX(-30px);
  }
}

.wind-arrow.colliding {
  animation: .3s ease-out collide;
}

@keyframes collide {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.5);
  }

  100% {
    transform: scale(1);
  }
}

#world-map {
  width: 100%;
  height: 100%;
  position: relative;
}
